@layer base {
	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		@apply font-bold;
	}

	h1 {
		@apply text-5xl;
	}

	h2 {
		@apply text-4xl;
	}

	h3 {
		@apply text-3xl;
	}

	h4 {
		@apply text-2xl;
	}

	h5 {
		@apply text-xl;
	}

	h6 {
		@apply text-lg;
	}

	p {
		@apply text-base;
	}

	small {
		@apply text-xs;
	}

	::selection {
		background: #b3d4fc;
		text-shadow: none;
	}

	small,
	kbd,
	abbr,
	code {
		display: inline-block;
		vertical-align: top;
	}

	pre {
		@apply my-6;
	}

	blockquote {
		@apply border-solid border border-gray-500 py-6 pl-6;
		margin: 0 0 24px 0;
		display: block;
		font-style: italic;

		p {
			display: inline;
		}

		cite {
			@apply text-xs;
			display: block;
		}
	}

	cite {
		font-style: italic;

		&:before {
			content: '\2014 \00A0';
		}
	}
}
